@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700);
:root{--backgroundColor: rgba(246, 241, 209);--colorShadeA: rgb(106, 163, 137);--colorShadeB: rgb(121, 186, 156);--colorShadeC: rgb(150, 232, 195);--colorShadeD: rgb(187, 232, 211);--colorShadeE: rgb(205, 255, 232)}*{box-sizing:border-box}*::before,*::after{box-sizing:border-box}body{font-family:"OpenSans", sans-serif;font-size:1rem;line-height:2;display:flex;align-items:center;justify-content:center;margin:0;min-height:100vh;background:rgba(246, 241, 209);background:var(--backgroundColor)}button{position:relative;display:inline-block;cursor:pointer;outline:none;border:0;vertical-align:middle;text-decoration:none;font-size:1.5rem;color:rgb(106, 163, 137);color:var(--colorShadeA);font-weight:700;text-transform:uppercase;font-family:inherit}button.big-button{padding:1em 2em;border:2px solid rgb(106, 163, 137);border:2px solid var(--colorShadeA);border-radius:1em;background:rgb(205, 255, 232);background:var(--colorShadeE);-webkit-transform-style:preserve-3d;transform-style:preserve-3d;transition:all 175ms cubic-bezier(0, 0, 1, 1)}button.big-button::before{position:absolute;content:"";width:100%;height:100%;top:0;left:0;right:0;bottom:0;background:rgb(150, 232, 195);background:var(--colorShadeC);border-radius:inherit;box-shadow:0 0 0 2px rgb(121, 186, 156),0 0.75em 0 0 rgb(106, 163, 137);box-shadow:0 0 0 2px var(--colorShadeB),0 0.75em 0 0 var(--colorShadeA);-webkit-transform:translate3d(0, 0.75em, -1em);transform:translate3d(0, 0.75em, -1em);transition:all 175ms cubic-bezier(0, 0, 1, 1)}button.big-button:hover{background:rgb(187, 232, 211);background:var(--colorShadeD);-webkit-transform:translate(0, 0.38em);transform:translate(0, 0.38em)}button.big-button:hover::before{-webkit-transform:translate3d(0, 0.75em, -1em);transform:translate3d(0, 0.75em, -1em)}button.big-button:active{-webkit-transform:translate(0, 0.75em);transform:translate(0, 0.75em)}button.big-button:active::before{-webkit-transform:translate3d(0, 0, -1em);transform:translate3d(0, 0, -1em);box-shadow:0 0 0 2px rgb(121, 186, 156),0 0.25em 0 0 rgb(121, 186, 156);box-shadow:0 0 0 2px var(--colorShadeB),0 0.25em 0 0 var(--colorShadeB)}.app-name{position:relative;text-align:center;font-size:3rem;color:rgb(106, 163, 137);color:var(--colorShadeA);font-weight:700;text-transform:uppercase;font-family:inherit}

.clock{padding-bottom:20px;text-align:center;font-size:2rem}.clock-info{color:#6aa389;font-weight:700;text-transform:uppercase;font-family:inherit}.clock-time{display:block;color:#6aa389}

